import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ColumnTypeMappingViewModel } from "../../Model/ViewModel/ColumnTypeMappingViewModel";

/**
 * Generated class for the ColumnTypeMappingModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-column-type-mapping-add-edit-modal",
  templateUrl: "column-type-mapping-add-edit-modal.html"
})
export class ColumnTypeMappingAddEditModalPage {
  item: ColumnTypeMappingViewModel;
  mode: string;
  title: string;
  CanEditJob: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.item = new ColumnTypeMappingViewModel();
    this.item.columnname = navParams.data.item.columnname;
    this.item.column_type_key = navParams.data.item.column_type_key;
    this.item.column_type = navParams.data.item.column_type;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;

    this.mode = navParams.data.mode;
    this.CanEditJob = navParams.data.CanEditJob;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectColumnType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.column_type_key,
      code_type: "010",
      code_key: "",
      title: "欄位類型"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.column_type_key = select_data.code_key;
      this.item.column_type = select_data.code_desc;
    });

    modal.present();
  }
  SelectColumn() {
    let modal = this.modalCtrl.create(
      "GlossarySelectModalPage",
      {
        eng_name: this.item.columnname
      }
    );
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.columnname = select_data.eng_name;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ColumnTypeMappingModalPage");
  }
}



